main {
  display: flex;
  flex-wrap: wrap;
  max-width: 1080px;
  margin: 2rem auto 4rem;

  @media screen and (max-width: 1080px) {
    max-width: 780px;
  }

.Tilt {
  width: calc(50% - 6rem);
  display: flex;
  margin: 2rem 3rem;
  align-items: center;
  justify-content: center;
  position: relative;
  transform-style: preserve-3d;

  .heading, .information {
  }

  .title, .website-link, .track {
    transform: translateZ(80px);
  }

  &:hover {
    box-shadow: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.5));
  }

  @media screen and (min-width: 1080px) {
    &:nth-child(even) {
      top: 6rem;
    }
  }

  @media screen and (max-width: 1080px) {
    width: 100%;
  }

  // &:nth-child(even):last-child:not(:only-child) {
  }

.animated {
  padding: 2rem 5rem 2rem 2.5rem;
  text-align: left;
  display: flex;
  height: 100%;
  transition: all 300ms ease;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  transform: translateZ(20px);
}
  
  @media screen and (max-width: 1080px) {
    padding: 4rem 3rem;
    width: 100%;
  }

  @media screen and (max-width: 500px) {
    margin: 0 0 2rem;
  }

}

.information {
  max-width: 576px;
  margin: 0 auto;
  width: 100%;
  position: relative;
}

.heading {
  @media screen and (max-width: 500px) {
    text-align: center;
  }

  .title {
    font-size: 3.55rem;
    font-family: "Aquatico";
    line-height: 100%;
    letter-spacing: 3px;
    font-weight: bold;
    margin-left: -5.5rem;

    @media screen and (min-width: 768px) {
      letter-spacing: 0.5rem;
    }
    @media screen and (max-width: 1080px) {
      width: calc(100% - 5rem);
      margin: 0;
    }

    @media screen and (max-width: 500px) {
      word-break: break-word;
      width: 100%;
      font-size: 2rem;
    }
  }

  .subtitle {
    font-size: 1.25rem;
    margin-top: 0.5rem;
  }
}

.text {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
   
  i {
    position: absolute;
    right: -6rem;
    top: 0;
    font-size: 1rem;
    padding: 0.45rem 0.4rem;
    border-radius: 1rem;
    z-index: 4;
    cursor: pointer;

    @media screen and (max-width: 1080px) {
      right: 0;
    }

    &:hover + .description {
      opacity: 1;
      pointer-events: auto;
    }
  }

  @media screen and (min-width: 768px) {
  }

  .description {
    width: 100%;
    height: auto;
    padding: 2rem;
    position: absolute;
    top: 0;
    left: 0;
    font-size: 16px;
    line-height: 150%;
    font-weight: bold;
    opacity: 0;
    transition: opacity 300ms linear;
    pointer-events: none;
    z-index: 3;

    @media screen and (min-width: 1080px) {
      font-size: 18px;
      width: calc(100% + 2.5rem);
    }
  }
}

.tags {
  font-size: 0.75rem;
  margin: 1rem 0;
  font-weight: bold;
  letter-spacing: -0.5px;

  @media screen and (min-width: 768px) {
    font-size: 0.65rem;
    margin: 0;
    position: absolute;
    right: -5.3rem;
    top: 3.25rem;
  }

  @media screen and (max-width: 1080px) {
    right: 0;
  }

  li {
    display: inline-block;
    border: 1px solid rgb(187, 187, 187);
    margin: 2px;
    padding: 0.35rem;
    border-radius: 2px;

    @media screen and (min-width: 768px) {
      &:not(:last-child) {
        margin: 0 0 0.5rem;
      }
      padding: 0.25rem 0.5rem;
      border-width: 1px;
      border-style: solid;
      border-right-width: 5px;
      display: block;
      margin: 0;
    }
  }
}

a.website-link {
  margin-top: 1rem;
  font-size: 18px;
  display: block;
  padding: 0.5rem 1.25rem;
  color: #222222;
  border-radius: 3px;
  background: linear-gradient(
    322deg,
    rgba(249, 249, 249, 0.3) 4%,
    rgba(249, 249, 249, 0.8)
  );
  display: flex;
  align-items: center;
  transform: perspective(1px) translateZ(0);
  position: relative;
  transition: color 300ms ease, top 300ms ease, box-shadow 300ms ease;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  font-weight: bold;
  border-bottom-width: 3px;
  border-bottom-style: solid;
  z-index:2;
  top: 0;

  @media screen and (min-width: 768px) {
    display: inline-block;
  }

  @media screen and (max-width: 500px) {
    border-top-width: 3px;
    border-top-style: solid;
    font-size: 1rem;
    background: linear-gradient(
      322deg,
      rgba(249, 249, 249, 0.4) 4%,
      rgba(249, 249, 249, 0.9)
    );
  }

  span {
    color: #5bc543;
    position: absolute;
    left: -45px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 0.75rem;
    background: rgba(225, 250, 218, 0.9);
    padding: 4px 0.5rem 0.4rem;
    border-radius: 10px;
    opacity: 0;
    transition: opacity 300ms ease;

    i {
      margin: 0 2px 0 1px;
      font-size: 0.6rem;
    }

    @media screen and (max-width: 500px) {
      position: static;
      background: rgba(225, 250, 218, 0.5);
      transform: none;
      opacity: 1;
      padding: 2px 0.5rem 4px;
      margin-right: 7px;
      margin-top: 4px;
    }
  }

  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    border-radius: 5px;
    transform: scaleX(0);
    transform-origin: 0 50%;
    transition-property: transform;
    transition-duration: 0.5s;
    transition-timing-function: ease-out;
  }
  &:hover,
  &:focus,
  &:active {
    color: #24afda;
    top: 3px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);

    span {
      opacity: 1;
    }
  }
  &:hover:before,
  &:focus:before,
  &:active:before {
    transform: scaleX(1);
    transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
  }
}

.links {
  padding: 1px 10px;
  position: relative;
  z-index: 3;
  @media screen and (max-width: 600px) {
    display: none;
  }

  a.github,
  a.designs {
    padding: 5px 10px 7px;
    display: inline-block;
    transition: background 300ms ease;

    i {
      margin-right: -0.25rem;
      font-size: 1rem;
    }
  }

  a.github {
    &:hover {
      background: rgba(85, 39, 117, 0.3);
    }
    i {
      color: #552775;
    }
  }

  a.designs {
    &:hover {
      background: rgba(232, 62, 128, 0.3);
    }
    i {
      color: #e83e80;
    }
  }
}

.eyebrow {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  font-size: 12px;

  @media screen and (max-width: 500px) {
    justify-content: center;
  }
}

.role {
  padding: 5px 10px 7px 20px;
  position: relative;
  display: inline-block;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0.5);
  border: 1px solid white;
  font-weight: bold;

  &::before {
    width: 26px;
    height: auto;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: -13px;
    z-index: 3;
  }

  &.Forum {
    &::before {
      content: url("./../f1.svg");
    }
  }

  &.Lehigh {
    &::before {
      width: 20px;
      left: -10px;
      top: calc(50% + 1px);
      content: url("./../lehigh.svg");
    }
  }

  &.Project {
    &::before {
      content: url("./../ram-logo-white.svg");
    }
  }

  &.Weidenhammer {
    &::before {
      width: 24px;
      left: -11px;
      top: calc(50% + 1px);
      content: url("./../weidenhammer.svg");
    }
  }
}

.track {
  display: flex;
  margin: 0 auto;
  max-width: 1080px;
  width: 100%;

  .row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 1.5rem -6rem 0 -3.5rem;
    grid-gap: 1rem;

    @media screen and (max-width: 1080px) {
      margin: 1.5rem -6rem 0;
    }

    @media screen and (max-width: 600px) {
      grid-template-rows: 1fr 1fr 1fr;
      grid-template-columns: 1fr;
      margin: 0 -1rem;
    }

    > * {
      width: 100%;
    }
  }

  .three {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .two {
    display: grid;
    grid-gap: 1rem;
    margin: 1.5rem -6rem 0 -3.5rem;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;

    @media screen and (max-width: 1080px) {
      margin: 1.5rem -6rem 0;
    }

    @media screen and (max-width: 600px) {
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr;
      margin: 0 -1rem;
    }

    > * {
      width: 100%;
    }
  }
}